.presContainer{
    background-image: url(../img/white-concrete-wall.png);
    background-size: cover;
    height:800px;
    @media only screen and (max-width: 950px) {
        height: 1300px;
    }
    h2{
        margin-top: 50px;
        margin-bottom: 100px;
    }
}
.photos{
    margin-bottom: 50px;
    @media only screen and (max-width: 950px) {
        flex-direction: column;
        align-items: center;
    }
}

.carouselTeam{
    margin-right: 20px;
    margin-top: 20px;
    @media only screen and (max-width: 950px) {
        margin-top: 0;
        margin: 20px;
    }
}

.texte{
    position: relative;
    margin-top: 20px;
    width: 450px;
    height: 180px;
    text-align: center;
    background: white;
    padding: 10px;
    border-radius: 10px;
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    font-style: italic;
    margin: 0 auto;
    @media only screen and (max-width: 950px) {
        margin-top: 20px;
        margin-bottom: 40px;
        font-size: 0.9em;
    }
    
    @media only screen and (max-width: 800px) {
        width: 400px;
       
    }
    @media only screen and (max-width: 550px) {
        font-size: 0.8em;
        width: 300px;
    }
    @media only screen and (max-width: 350px) {
        font-size: 0.7em;
        width: 250px;
    }
}

.photoTextContainer{
    position: relative;
   
}



.team{
        width: 450px;
        height:285px;
        border-radius: 10px;
        @media only screen and (max-width: 950px) {
            margin: 20px;
            width: 400px;
            height: 280px;
        }
        @media only screen and (max-width: 470px) {
            margin: 20px;
            width: 300px;
            height: 220px;
        }
}




