* {
  box-sizing: border-box;
  list-style-type: none;
}

* body {
  margin: 0;
  font-family: var(--font-family3);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--text-color);
}

* h1 {
  font-family: var(--font-family2);
  margin: auto 0;
  font-size: 1.7em;
}

@media only screen and (max-width: 1250px) {
  * h1 {
    font-size: 1.3em;
  }
}

@media only screen and (max-width: 1000px) {
  * h1 {
    font-size: 1.2em;
  }
}

* h2 {
  font-family: var(--font-family1);
  font-size: 2em;
}

@media only screen and (max-width: 450px) {
  * h2 {
    font-size: 1.8em;
  }
}

* .btn-mention {
  border: none;
  background: none;
}

:root {
  --primary: #91D6DF ;
  --font-family1: 'Amatic SC', cursive;
  --font-family2: 'Train One', cursive;
  --font-family3: 'Urbanist', sans-serif;
  --box-shadow: 0 6px 9px 0 rgb(0 0 0 / 15%) ;
  --box-shadow-nav: rgb(0 0 0 / 22% ) 0px 2px 18px 0px;
  --text-color: #444;
}

.p-5 {
  padding: 5px;
}

.p-10 {
  padding: 10px;
}

.p-20 {
  padding: 20px;
}

.p-30 {
  padding: 30px;
}

.m-5 {
  margin: 5px;
}

.m-10 {
  margin: 10px;
}

.m-20 {
  margin: 20px;
}

.mb-20 {
  margin-bottom: 20px;
}

.d-flex {
  display: flex;
}

.flex-row {
  flex-direction: row;
}

.flex-column {
  flex-direction: column;
}

.flex-fill {
  flex: 1 1 auto;
}

.justify-content-center {
  justify-content: center;
}

.align-items-center {
  align-items: center;
}

.b1 {
  background-color: red;
  border: 1px solid red;
}

.b2 {
  background-color: green;
  border: 1px solid green;
}

.b3 {
  background-color: blue;
  border: 1px solid blue;
}

.b4 {
  background-color: purple;
  border: 1px solid purple;
}

.header {
  height: 800px;
  background-image: url(../img/Acceuil.png);
  background-size: cover;
  font-size: 17px;
}

.header h1 {
  font-family: var(--font-family2);
  margin: auto 0;
  font-size: 1.7em;
}

@media only screen and (max-width: 1250px) {
  .header h1 {
    font-size: 1.3em;
  }
}

@media only screen and (max-width: 1000px) {
  .header h1 {
    font-size: 1.2em;
  }
}

@media only screen and (max-width: 450px) {
  .header h1 {
    font-size: 1em;
  }
}

@media only screen and (max-width: 870px) {
  .logoTitle {
    margin: 0 auto;
    flex-direction: column;
    align-items: center;
  }
}

.logo {
  height: 80px;
  margin: 20px;
}

@media only screen and (max-width: 870px) {
  .logo {
    height: 80px;
    width: 80px;
  }
}

.headerContent {
  justify-content: space-between;
}

.navBar {
  margin: 20px;
}

@media only screen and (max-width: 1000px) {
  .navBar {
    display: none;
  }
}

.navBar li {
  margin-top: 37px;
  padding-right: 50px;
  cursor: pointer;
  font-family: var(--font-family3);
  transition: all 0.3s;
}

@media only screen and (max-width: 1250px) {
  .navBar li {
    padding-right: 30px;
    font-size: 0.9em;
  }
}

.navBar li:hover {
  transform: scale(1.2);
  transition: all 0.3s;
  font-weight: bold;
}

@media only screen and (max-width: 1000px) {
  .navBar li {
    display: none;
  }
}

.bars {
  display: none;
}

@media only screen and (max-width: 1000px) {
  .bars {
    display: block;
    margin: 50px 20px;
    position: fixed;
    right: 0;
    z-index: 10000;
    cursor: pointer;
    transition: all 0.2s;
  }
  .bars:hover {
    transform: scale(1.2);
    transition: all 0.2s;
  }
}

.navBarResponsive {
  position: fixed;
  z-index: 10000;
  right: 0;
  margin: 50px;
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.476);
  border-radius: 10px;
}

.navBarResponsive li {
  margin: 10px;
}

.accueil {
  align-items: center;
}

@media only screen and (max-width: 870px) {
  .accueil {
    flex-direction: column;
  }
}

@media only screen and (max-width: 450px) {
  .accueil h2 {
    width: 250px;
  }
}

.descriptions {
  width: 400px;
  height: 500px;
  justify-content: center;
  margin-left: 50px;
}

@media only screen and (max-width: 450px) {
  .descriptions {
    width: 250px;
  }
}

@media only screen and (max-width: 450px) {
  .descriptions p {
    font-size: 0.9em;
    width: 250px;
  }
}

.picPoke img {
  height: 250px;
  width: 250px;
}

@media only screen and (max-width: 1200px) {
  .picPoke img {
    height: 220px;
    width: 220px;
  }
}

@media only screen and (max-width: 1000px) {
  .picPoke img {
    height: 200px;
    width: 200px;
  }
}

@media only screen and (max-width: 880px) {
  .picPoke img {
    height: 190px;
    width: 190px;
  }
}

@media only screen and (max-width: 450px) {
  .picPoke img {
    height: 120px;
    width: 120px;
  }
}

.picPoke .pic1 {
  transform: translateX(150%) translateY(-20%);
  filter: drop-shadow(20px 20px 32px #000000);
}

@media only screen and (max-width: 1250px) {
  .picPoke .pic1 {
    transform: translateX(50%) translateY(-20%);
  }
}

@media only screen and (max-width: 870px) {
  .picPoke .pic1 {
    transform: translateX(0%) translateY(-20%);
  }
}

.picPoke .pic2 {
  filter: drop-shadow(20px 20px 32px #000000);
  transform: translateX(120%) translateY(40%);
}

@media only screen and (max-width: 1250px) {
  .picPoke .pic2 {
    transform: translateX(20%) translateY(40%);
  }
}

@media only screen and (max-width: 870px) {
  .picPoke .pic2 {
    transform: translateX(-20%) translateY(-20%);
  }
}

.footerContainer {
  background-image: url(../img/healthy-diet-background.png);
  height: 550px;
  justify-content: space-around;
  background-size: cover;
  position: relative;
}

.footerContainer p {
  cursor: pointer;
  transition: all 0.2s;
}

.footerContainer p:hover {
  transform: translateX(5px);
  transition: all 0.2s;
}

@media only screen and (max-width: 670px) {
  .footerContainer {
    flex-direction: column;
    text-align: center;
    height: 700px;
  }
}

@media only screen and (max-width: 470px) {
  .footerContainer {
    height: 1000px;
  }
}

.googleMap {
  margin-top: 10px;
}

@media only screen and (max-width: 850px) {
  .googleMap iframe {
    height: 270px;
    width: 300px;
  }
}

@media only screen and (max-width: 320px) {
  .googleMap iframe {
    height: 240px;
    width: 280px;
  }
}

.adresse .logo {
  height: 40px;
  margin: 10px;
}

@media only screen and (max-width: 670px) {
  .adresse .logo {
    height: 40px;
    width: 40px;
    margin: 10px;
  }
}

.adresseCuisine {
  cursor: pointer;
  position: relative;
}

.signature {
  position: absolute;
  right: 10px;
  bottom: 0;
  font-size: 13px;
  opacity: 0.6;
  cursor: pointer;
}

.signature span {
  font-weight: bold;
  color: orange;
  border: 3px solid orange;
}

@media only screen and (max-width: 670px) {
  .signature {
    font-size: 0.5em;
  }
}

.btn-mention {
  font-style: italic;
  cursor: pointer;
  font-family: var(--font-family3);
  position: absolute;
  left: 10px;
  bottom: 0;
  font-size: 13px;
  margin: 10px;
}

.formules {
  justify-content: space-around;
  margin-top: 70px;
}

@media only screen and (max-width: 850px) {
  .formules {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.formules .for {
  background-image: url(../img/ardoise.jpeg);
  height: 550px;
  width: 250px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

@media only screen and (max-width: 1250px) {
  .formules .for {
    height: 500px;
    width: 200px;
  }
}

@media only screen and (max-width: 850px) {
  .formules .for {
    margin: 10px;
    height: 550px;
    width: 250px;
  }
}

.formules .for img {
  height: 220px;
  width: 250px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

@media only screen and (max-width: 1250px) {
  .formules .for img {
    width: 200px;
  }
}

@media only screen and (max-width: 850px) {
  .formules .for img {
    width: 250px;
  }
}

.formules .for p {
  color: white;
  font-family: var(--font-family1);
  margin: 30px;
  font-size: 1.2em;
  font-weight: bold;
}

@media only screen and (max-width: 1250px) {
  .formules .for p {
    margin: 20px;
  }
}

@media only screen and (max-width: 850px) {
  .formules .for p {
    margin: 30px;
  }
}

.formules .for .saladeInsta {
  cursor: pointer;
}

.formules .for .saladeInsta:hover {
  color: var(--primary);
}

.thumbs-wrapper {
  display: none;
}

.carouselSalad img {
  height: 220px;
  width: 250px;
}

.fork {
  height: 150px;
  width: 150px;
  margin: 100px auto;
}

.EachBlocs {
  justify-content: space-around;
  transform: translateY(-50%);
}

@media only screen and (max-width: 870px) {
  .EachBlocs {
    transform: translateY(0%);
  }
}

@media only screen and (max-width: 670px) {
  .EachBlocs {
    flex-direction: column;
    align-items: center;
    margin: 20px;
  }
}

.EachBlocs img {
  height: 30px;
  width: 30px;
  margin: 0 auto;
}

@media only screen and (max-width: 870px) {
  .EachBlocs img {
    height: 20px;
    width: 20px;
  }
}

.EachBlocs .emporter {
  height: 100px;
  width: 200px;
  opacity: 0.6;
}

@media only screen and (max-width: 1250px) {
  .EachBlocs .emporter {
    height: 100px;
    width: 180px;
  }
}

@media only screen and (max-width: 1000px) {
  .EachBlocs .emporter {
    height: 90px;
    width: 150px;
  }
}

@media only screen and (max-width: 870px) {
  .EachBlocs .emporter {
    height: 70px;
    width: 120px;
  }
}

@media only screen and (max-width: 670px) {
  .EachBlocs .emporter {
    height: 70px;
    width: 150px;
  }
}

.bloc, .bloc2 {
  background: white;
  width: 255px;
  height: 181px;
  background: rgba(255, 255, 255, 0.865);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 40px 0px;
  text-align: center;
  justify-content: center;
  transition: all 0.3s;
}

.bloc:hover, .bloc2:hover {
  transform: scale(1.2);
  transition: all 0.3s;
}

@media only screen and (max-width: 1250px) {
  .bloc, .bloc2 {
    width: 225px;
    height: 151px;
  }
}

@media only screen and (max-width: 1000px) {
  .bloc, .bloc2 {
    width: 200px;
    height: 141px;
  }
}

@media only screen and (max-width: 870px) {
  .bloc, .bloc2 {
    width: 160px;
    height: 120px;
  }
}

@media only screen and (max-width: 670px) {
  .bloc, .bloc2 {
    width: 250px;
    margin: 10px;
  }
}

.bloc p, .bloc2 p {
  font-weight: bold;
  padding: 10px;
}

@media only screen and (max-width: 870px) {
  .bloc p, .bloc2 p {
    font-size: 0.7em;
  }
}

.bloc2 {
  background: rgba(145, 214, 223, 0.5);
}

.content {
  text-align: center;
  background-image: url(../img/kitchen.png);
  background-size: cover;
  height: 100%;
}

.presContainer {
  background-image: url(../img/white-concrete-wall.png);
  background-size: cover;
  height: 800px;
}

@media only screen and (max-width: 950px) {
  .presContainer {
    height: 1300px;
  }
}

.presContainer h2 {
  margin-top: 50px;
  margin-bottom: 100px;
}

.photos {
  margin-bottom: 50px;
}

@media only screen and (max-width: 950px) {
  .photos {
    flex-direction: column;
    align-items: center;
  }
}

.carouselTeam {
  margin-right: 20px;
  margin-top: 20px;
}

@media only screen and (max-width: 950px) {
  .carouselTeam {
    margin-top: 0;
    margin: 20px;
  }
}

.texte {
  position: relative;
  margin-top: 20px;
  width: 450px;
  height: 180px;
  text-align: center;
  background: white;
  padding: 10px;
  border-radius: 10px;
  box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  font-style: italic;
  margin: 0 auto;
}

@media only screen and (max-width: 950px) {
  .texte {
    margin-top: 20px;
    margin-bottom: 40px;
    font-size: 0.9em;
  }
}

@media only screen and (max-width: 800px) {
  .texte {
    width: 400px;
  }
}

@media only screen and (max-width: 550px) {
  .texte {
    font-size: 0.8em;
    width: 300px;
  }
}

@media only screen and (max-width: 350px) {
  .texte {
    font-size: 0.7em;
    width: 250px;
  }
}

.photoTextContainer {
  position: relative;
}

.team {
  width: 450px;
  height: 285px;
  border-radius: 10px;
}

@media only screen and (max-width: 950px) {
  .team {
    margin: 20px;
    width: 400px;
    height: 280px;
  }
}

@media only screen and (max-width: 470px) {
  .team {
    margin: 20px;
    width: 300px;
    height: 220px;
  }
}
