.footerContainer{
    background-image: url(../img/healthy-diet-background.png);
    height: 550px;
    justify-content: space-around;
    background-size: cover;
    position: relative;

    p{
        cursor: pointer;
        transition: all 0.2s;
        &:hover{
            transform: translateX(5px);
            transition: all 0.2s;
        }
    }
    @media only screen and (max-width: 670px) {
        flex-direction: column;
       text-align: center;
       height: 700px;
    }
    @media only screen and (max-width: 470px) {
       
       height: 1000px;
    }
}

.googleMap{
    margin-top: 10px;
  
    iframe{
        @media only screen and (max-width: 850px) {
            height: 270px;
            width: 300px;
        }
        @media only screen and (max-width: 320px) {
            height: 240px;
            width: 280px;
        }
    }
    
}

.adresse{

    .logo{
        height: 40px;
        margin: 10px;
        @media only screen and (max-width: 670px) {
            height: 40px;
            width: 40px;
            margin: 10px;
        }
    }

}

.adresseCuisine{
    cursor: pointer;
    position: relative;
    
}

.signature{
    position: absolute;
    right: 10px;
    bottom: 0;
    font-size: 13px;
    opacity: 0.6;
    cursor: pointer;
    span{
        font-weight: bold;
        color: orange;
        border: 3px solid orange;
    }
    @media only screen and (max-width: 670px) {
        font-size: 0.5em;
    }
}

.btn-mention{
    font-style: italic;
    cursor: pointer;
    font-family: var(--font-family3);
    position: absolute;
    left: 10px;
    bottom: 0;
    font-size: 13px;
    margin: 10px;
}