.header{
    height: 800px;
    background-image: url(../img/Acceuil.png);
    background-size:cover;
    font-size: 17px;

    h1{
        font-family: var(--font-family2);
        margin: auto 0;
        font-size: 1.7em;
        @media only screen and (max-width: 1250px) {
            font-size: 1.3em;
        }
        @media only screen and (max-width: 1000px) {
            font-size: 1.2em;
        }
        @media only screen and (max-width: 450px) {
            font-size: 1em;
        
         }
    }

}

.logoTitle{
    @media only screen and (max-width: 870px) {
        margin: 0 auto;
        flex-direction: column;
        align-items: center;
    }

}
.logo{
    height: 80px;
    margin: 20px;
    @media only screen and (max-width: 870px) {
        height: 80px;
        width: 80px;
    }
}
.headerContent{
    justify-content: space-between;
}
.navBar{
    margin: 20px;
    @media only screen and (max-width: 1000px) {
        display: none;
    }
   li{
       margin-top: 37px;
       padding-right: 50px;
       cursor: pointer;
       font-family: var(--font-family3);
       transition: all 0.3s;
       @media only screen and (max-width: 1250px) {
        padding-right: 30px;
        font-size: 0.9em;
        }
       &:hover{
           transform: scale(1.2);
           transition: all 0.3s;
           font-weight: bold;
       }
        @media only screen and (max-width: 1000px) {
            display: none;
        }
   }
}

.bars{
    display: none;
    @media only screen and (max-width: 1000px) {
        display: block;
        margin: 50px 20px;
        position: fixed;
        right: 0;
        z-index: 10000;
        cursor: pointer;
        transition: all 0.2s;
        &:hover{
            transform: scale(1.2);
            transition: all 0.2s;
        }
    }
}

.navBarResponsive{
    position: fixed;
    z-index: 10000;
    right: 0;
    margin: 50px;
    backdrop-filter: blur(10px);
    background: rgba(255, 255, 255, 0.476);
    border-radius: 10px;
    li{
        margin: 10px;
    }
}

.accueil{
    align-items: center;
    @media only screen and (max-width: 870px) {
       flex-direction: column;
    } 
    @media only screen and (max-width: 450px) {
        h2{
            width: 250px;
        }
     }
}
.descriptions{
    width: 400px;
    height: 500px;
    justify-content: center;
    margin-left: 50px;
    @media only screen and (max-width: 450px) {
        width: 250px;
    }
    p{
        @media only screen and (max-width: 450px) {
            font-size: 0.9em;
            width: 250px;
        }
    }
}

.picPoke{
    img{
        height: 250px;
        width: 250px;
        @media only screen and (max-width: 1200px) {
            height: 220px;
            width: 220px;
        }
        @media only screen and (max-width: 1000px) {
            height: 200px;
            width: 200px;
        }
        @media only screen and (max-width: 880px) {
            height: 190px;
            width: 190px;
        }
        @media only screen and (max-width: 450px) {
            height: 120px;
            width: 120px;
        }
    }
    .pic1{
        transform: translateX(150%) translateY(-20%);
        filter: drop-shadow(20px 20px 32px #000000);

        @media only screen and (max-width: 1250px) {
            transform: translateX(50%) translateY(-20%);
        }
        @media only screen and (max-width: 870px) {
             transform: translateX(0%) translateY(-20%);
         }
    }
    .pic2{
        filter: drop-shadow(20px 20px 32px #000000);
        transform: translateX(120%) translateY(40%);
        @media only screen and (max-width: 1250px) {
            transform: translateX(20%) translateY(40%);
        }
        @media only screen and (max-width: 870px) {
            transform: translateX(-20%) translateY(-20%);
        }
    }
}