.formules{
    justify-content: space-around;
    margin-top: 70px;
    @media only screen and (max-width: 850px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .for{
        background-image:url(../img/ardoise.jpeg);
        height: 550px;
        width: 250px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
        @media only screen and (max-width: 1250px) {
            height: 500px;
            width: 200px;
        }
        @media only screen and (max-width: 850px) {
            margin: 10px;
            height: 550px;
            width: 250px;
        }
        img{
            height: 220px;
            width: 250px;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            
            @media only screen and (max-width: 1250px) {
                width: 200px;
            }
            @media only screen and (max-width: 850px) {
                width: 250px;
            }
        }
        p{
            color: white;
            font-family: var(--font-family1);
            margin: 30px;
            font-size: 1.2em;
            font-weight: bold;
            
            @media only screen and (max-width: 1250px) {
                margin: 20px;
            }
            @media only screen and (max-width: 850px) {
                margin: 30px;
            }
          
        }
        .saladeInsta{
            cursor: pointer;
            &:hover{
                color: var(--primary);
            }
        }
    }
}

.thumbs-wrapper{
    display: none;
}

.carouselSalad{
    img{
        height: 220px;
        width: 250px;
       
    }
}

.fork{
    height: 150px;
    width: 150px;
    margin: 100px auto;
}

