.EachBlocs{
    justify-content: space-around;
    transform: translateY(-50%);
    @media only screen and (max-width: 870px) {
        transform: translateY(0%);
    }
    @media only screen and (max-width: 670px) {
        flex-direction: column;
        align-items: center;
        margin: 20px;
    }
    
    
    img{
        height: 30px;
        width: 30px;
        margin: 0 auto;
        @media only screen and (max-width: 870px) {
            height: 20px;
            width: 20px;
        }
    }
    .emporter{
        height: 100px;
        width: 200px;
        opacity: 0.6;
        @media only screen and (max-width: 1250px) {
            height: 100px;
            width: 180px;
        }
        @media only screen and (max-width: 1000px) {
            height: 90px;
            width: 150px;
        }
        @media only screen and (max-width: 870px) {
            height: 70px;
            width: 120px;
        }
        @media only screen and (max-width: 670px) {
            height: 70px;
            width: 150px;
        }
    }
}

.bloc, .bloc2{
    background: white;
    width: 255px;
    height: 181px;
    background: rgba(255, 255, 255, 0.865);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 40px 0px;
    text-align: center;
    justify-content: center;
    transition: all 0.3s;
    &:hover{
        transform: scale(1.2);
        transition: all 0.3s;
    }
    @media only screen and (max-width: 1250px) {
        width: 225px;
         height: 151px;
    }
    @media only screen and (max-width: 1000px) {
        width: 200px;
         height: 141px;
    }
    @media only screen and (max-width: 870px) {
        width: 160px;
         height: 120px;
    }
    @media only screen and (max-width: 670px) {
        width: 250px;
         margin: 10px;
    }
    p{
        font-weight: bold;
        padding: 10px;
        @media only screen and (max-width: 870px) {
            font-size: 0.7em;
        }
    }
}
.bloc2{
   
    background: rgba(145, 214, 223, 0.5);
}
